
import back from '@/components/back'
import XSwitch from 'vux/src/components/x-switch/index.vue'

export default {
components:{
back,
XSwitch
},
data(){
return {
list:null,
drMsg:this.$store.state.userInfo.Data.User,
stringValue:false,
money: 50,
num: 0
}
},
created(){
this.getList()
},
mounted(){
},
methods:{
getList(){
this.$get('PlatFormAPI/SpecialistHosGroup/QuerySpecialistReferralSet',{
DrId: this.drMsg.DoctorId
}).then(o => {
if(o.IsSpecialReferral == 0){
this.stringValue = false
}else{
this.stringValue = true
this.money = o.SpecialReferralAmount
}
})
},
moneyMsg(){
if(Number(this.money) > 99999){
this.$toast("接诊单价不能超过10万元")
}
},
goTo(){
if(Number(this.money) > 99999){
return this.$toast("接诊单价不能超过10万元")
}
if(this.stringValue){
this.num = 1
}else{
this.num = 0
}
this.$post('PlatFormAPI/SpecialistHosGroup/SaveSpecialistReferralSet',{
DrId:this.drMsg.DoctorId,
IsSpecialReferral: this.num,
SpecialReferralAmount: this.money
}).then(o => {
this.$toast("设置成功")
setTimeout(() => {
this.$router.push('/setReferralList')
}, 2000)
})
},

}
}



